html {
  font-family: "STKaiti";
  font-size: 10px;
  background-color: #ffffcc;
}

body {
  display: grid;
  grid-template-areas:
    "header header"
    "nav nav"
    "main main"
    "footer footer";
  gap: 10px;
}

header {
  grid-area: header;
}

header *:first-child {
  font-size: 3.2em;
  text-align: center;
  color: #99ccff;
}

nav {
  grid-area: nav;
  border-radius: 10px;
  background-image: linear-gradient(to bottom right, lightblue, pink);
}

li {
  font-size: 3.2em;
  display: inline;
}

.link {
  outline: none;
  text-decoration: none;
  display: inline-block;
  color: #0099cc;
  border-radius: 10px;
  padding: 12px;
  margin-right: 30px;
  background-color: bisque;
}

main {
  grid-area: main;
  display: grid;
  grid-template-areas:
    "article aside"
    "sakura sakura"
    "pictures pictures";
  grid-template-columns: 3fr 1fr;
  gap: 10px;
}

footer {
  grid-area: footer;
  font-size: 1.6em;
  background-color: #ccccff;
}

article {
  grid-area: article;
  font-size: 1.6em;
  text-indent: 2em;
  text-align: justify;
  padding: 5px;
  background-image: linear-gradient(to bottom right, lightblue, pink);
}

.article.dczr {
  color: #ffff99;
  text-align: center;
}

.article.jidian {
  inline-size: 20vw;
  float: inline-start;
  margin-inline-end: 20px;
}

.article.clear {
  clear: both;
}

.article.taoyan {
  inline-size: 25vw;
  float: inline-end;
  margin-inline-start: 20px;
}

.aside {
  grid-area: aside;
}

.sakura {
  grid-area: sakura;
  font-size: 2.4em;
  text-align: center;
  color: #99ccff;
}

.pictures {
  grid-area: pictures;
  font-size: 0;
  /* 使图片间距为0 */
  text-align: left;
  background: linear-gradient(to bottom right, lightblue, pink);
}

video {
  inline-size: 360px;
}

.picture {
  block-size: 256px;
}

.link:hover {
  background-color: pink;
}

.link:active {
  color: white;
}

.picture:hover {
  cursor: pointer;
}